.parent-cart{
    margin-top: 20px;
    margin-bottom: 50px;
    width: 1250px;
}
.parent-cart h1{
    margin-top: 30px;
    margin-left: 20px;
}
.parent-cart .box-cart-nolength{
    text-align: center;
    width: 400px;
    margin-left: 418px;
    margin-top: 50px;
}
.parent-cart .box-cart-nolength h3{
    margin-bottom:30px ;
    text-transform: uppercase;
}
.parent-cart .box-cart-nolength button{
    width: 115px;
    height: 30px;
    text-transform: capitalize;
    font-size: 15px;
    font-weight: 600;
    border: none;
    border-radius: 7px;
    background-color: yellow;
    color: black;
    transition: 0.3s;
}
.parent-cart .box-cart-nolength button:hover{
    background-color: black;
    color: rgb(236, 236, 236);
}
.ditels{
    display: flex;
    width: 80%;
    border-radius: 5px;
    border: 2px solid rgb(196, 196, 196);
    margin-left: 80px;
    margin-top: 50px;
}
.ditels img{
    width: 270px;
    height: 270px;
    margin-left: 20px;
}
.box-info-cart{
    display: flex;
    flex-wrap: wrap;
    margin-top: 90px;
    margin-left: 20px;
}
.box-info-cart .ditels-cart .cat-cart{
    font-size: 15px;
    font-weight: 600;
    color: rgb(124, 124, 124);
    margin-bottom: 3px;

}
.box-info-cart .ditels-cart h2{
    text-transform: uppercase;
    margin-bottom: 5px;
}
.box-info-cart .ditels-cart .price-cart{
    text-transform: uppercase;
    font-weight: 600;
    color: rgb(107, 107, 107);
    margin-bottom: 8px;

}
.box-info-cart .input-cart-212{
    margin-left: 50px;
    margin-top: 18px;
}
.box-info-cart .input-cart-212 button{
    margin-top: 5px;
    width: 30px;
    height: 25px;
    background-color: black;
    color: rgb(255, 255, 255);
    font-size: 18px;
    border: none;
    transition: 0.3s;
}
.box-info-cart .input-cart-212 button:hover{
    background-color: yellow;
    color: rgb(0, 0, 0);
}
.box-info-cart .input-cart-212 input{
    padding-left: 10px;
    height: 25px;
    width: 79px;
    font-size: 18px ;
    border: 1px solid rgb(230, 230, 230);
}
.box-info-cart .icon-cart{
    margin-top: 23px;
    margin-left: 10px;
    width: 30px;
    height: 26px;
    background-color: rgb(0, 0, 0);
    color: white;
    text-align: center;
    border-radius: 3px;
    transition: 0.3s;
}
.box-info-cart .icon-cart:hover{
    background-color: yellow;
    color: rgb(0, 0, 0);
}
.box-info-cart .icon-cart li {
    list-style: none;
}
.box-cart-2 .box-total {
    margin-left: 80px;
    margin-top: 70px;
    font-size: 25px;
}