.perant-footer-boxs{
    margin-top: 15px;
    padding-top: 30px;
    background-color: rgb(230, 230, 230);
    width: 1264px;
    height: 400px;
    display: flex;
}
.box-footer-left{
    color: rgb(66, 66, 66);
    width: 30%;
    height: 88%;
    border-right: 3px solid rgb(145, 145, 145);
    padding-top: 40px;
    margin-left: 40px;
}
.box-left-info-footer{
    display: flex;
    font-size: 15px;
    text-transform: capitalize;
    font-family: sans-serif;
    margin-top: 15px;

}
.box-footer-left h4{
    text-transform: uppercase;
    font-size: 18px;
}
.icon-footer-1{
    margin-left: 45px;
    margin-right: 20px;
    margin-bottom: 18px;
    padding-top: 1px;
    font-size: 30px;

}
.box-footer-right{
    color: rgb(66, 66, 66);
    width: 66%;
    margin-left: -3%;
}
.parent-box-right-footer{
    text-align: center;
}
.parent-box-right-footer h1{
    font-family: cursive;
    font-size: 50px;
    margin-bottom: 10px;
}
.parent-box-right-footer .welc-1{
    margin-bottom: 10px;
    font-weight: 600;
}
.info-right-footer{
    display: flex;
    justify-content: space-between;
    margin: 70px;
    font-size: 14px;
}
.info-right-footer h5{
    color: rgb(0, 0, 0);
    padding-left: 40px;
    margin-bottom: 5px;
    font-size: 16px;
    text-transform: uppercase;
}
.info-right-footer ul li{
    list-style: none;
    margin-bottom: 3px;
    font-weight: 500;
}
.info-right-footer ul li:hover{
    color: rgb(0, 0, 0);
}