.box-two .box_product .contener-product{
   width: 250spx;
   height: 350px;
   margin-right: 10px;
   margin-bottom: 20px;
   border: 1px solid rgb(231, 231, 231);
   overflow: hidden ;
   border-radius: 5px;
}
.box-two .box_product .contener-product img{
    background-color: rgb(202, 202, 202);
