    text-transform: capitalize;
    font-weight: 600;
}
.box-two .box_product .contener-product button{
    margin-left: 30%;
    margin-top: 10px;
    width: 100px;
    height: 30px;
    border-radius: 5px;
    border: none;
    background-color: black;
    color: white;
    text-transform: capitalize;
    font-weight: 600;
    transition: 0.3s;
}
.box-two .box_product .contener-product button:hover{
    background-color: yellow ;
    color: rgb(0, 0, 0);
}
.box-two .box_product .contener-product .box_icon-1{
    display: flex;
    flex-direction: column;
    margin-left: 80%;
    position: relative;
    top: 34px;
    left: 51px;
    transition: 0.3s;
}
.box-two .box_product .contener-product:hover > .box_icon-1 {
    position: relative;
    top: 34px;
    left: 0px
}
.box-two .box_product .contener-product .box_icon-1 li{
    list-style: none;
}

.box-two .box_product .contener-product .icon_con{
    margin-bottom: 5px;
    width: 40px;
    height: 40px;
    background-color: black;
