.parent-parent-shop{
    width: 1403px;
}
.box-parent-shop{
    display: flex;
}
h1{
    margin-left: 20px;
    margin-top: 20px;
    margin-bottom: 5px;
    font-weight: 600;
    font-family: sans-serif;
    text-transform: uppercase;
}
.p-on-shop{
    margin-left: 20px;
    margin-bottom: 20px;
    font-weight: 500;
}
.box-own-1{
    margin-left: 10px;
    padding: 2px;
    background-color: rgb(231, 231, 231);
    width: 250px;
    height: 287px;
    border-radius: 5px;
}
.box-own-1 h3{
    text-transform: uppercase;
    font-size: 16px;
    margin-bottom: 10px;
    margin-top: 2px;
    padding-left: 10px;
    padding-top: 2px;
}
.box-own-1 .selects-product{
    background-color: white;
    width: 245px;
    height: 250px;
    border-radius: 5px;
    padding-top: 10px;
}
.box-own-1 .selects-product ul li{
    margin-bottom: 5px;
    padding-top: 5px;
    width: 20px;
    color: rgb(104, 103, 103);
    list-style: none ;
    text-transform: uppercase;
    font-weight: 600;
}
.box-own-1 .selects-product ul li:hover{
    color: black;
}
.img-shop {
    width: 250px;
    height: 350px;
}
.img-shop img{
    margin-top: 10px;
    margin-left: 10px;
    width: 250px;
    height: 300px; 
    border-radius: 2px;
}
.img-in-content-shop{
    margin-left: 50px;
    width: 967px;}
.img-in-content-shop img{
    width: 900px;
}
.product-shop{
    width: 85%;
    margin-left: 79px;
    margin-top: 20px;
    margin-bottom: 40px;
    display: flex;
    flex-wrap:wrap ;
}
.box-product-shop-map{
padding-left: 2px;
margin-right: 7px;
margin-bottom: 35px;
padding-top: 1px;
width: 258px;
height: 365px;
border: 2px solid rgb(168, 168, 168);
border-radius: 5px;
}
.box-own-2 .product-shop .img-map img{
    width: 250px;
    height: 250px;
    margin-top: -11%;
}
.box-own-2 .product-shop .img-map{
    overflow: hidden
}
.icon-shop-map-1{
    position: relative;
    top: 50px;
    left: 13px;
    margin-left: 190px;
    padding-top: 11px;
    padding-left: 1px;
    background-color: rgb(0, 0, 0);
    color: white;
    width: 40px;
    height: 40px;
    text-align: center;
    border-radius: 5px;
    transition: 0.3s;
}
.icon-shop-map-1:hover{
    background-color: yellow;
    color: rgb(0, 0, 0);
}
.icon-shop-map-2{
    position: relative;
    top: 58px;
    left: 13px;
    margin-left: 190px;
    padding-top: 11px;
    padding-left: 1px;
    background-color: rgb(0, 0, 0);
    color: white;
    width: 40px;
    height: 40px;
    text-align: center;
    border-radius: 5px;
    transition: 0.3s;
}
.icon-shop-map-2:hover{
    background-color: yellow;
    color: rgb(0, 0, 0);
}
.box-product-shop-map .info-shop-map{
    margin-left: 50px;
    margin-top: 5px;
    margin-bottom: 20px;
}
.box-product-shop-map .info-shop-map .name-info-shop-map{
    text-transform: capitalize;
    font-weight: 600;
}
.box-product-shop-map .info-shop-map .price-info-shop-map{
    font-weight: 500;
}
.box-product-shop-map button{
    margin-left: 75px;
    width: 100px;
    height: 30px;
    background-color: black;
    color: white;
    text-transform: capitalize;
    border: none;
    border-radius: 5px;
    transition: 0.3s;
}
.box-product-shop-map button:hover{
    background-color: yellow;
    color: black;
    font-weight: 600;
}