*{
    margin: 0% ;
    box-sizing: border-box;
}
.parent{
    width: 1264px;
    height: 205px;
}
.navup{
    padding: 10px 20px;
    width: 100%;
    height: 50px;
    background-color: rgb(219, 219, 219);
}
.boxnavup{
    display: flex;
    margin-top: 2px;
}
.icon{
    font-size: 20px;
    margin-right: 10px;
    margin-top: 6px;
}
.info-header-1{
    margin-top: 8px;
    text-transform: capitalize;
    font-weight: 300 ;
}

.nav2{
    width: 100%;
    height: 70px;
}
.allsearch {
    display: flex;
    margin-left: 184px;
}
.boxnav2
{
    display: flex;
    justify-content: space-between;
    margin-left: 20px;
    margin-bottom: 9px;
    margin-right: 10px;
    padding-top: 21px;
}
.icon2{
    padding-top: -4%;
}
.iconnav2{
    font-size: 30px;
}
.allsearch input {
    padding-left: 5px;
    padding-right: 5px;
    width: 200px;
    height: 25px;
    border: 1px solid rgb(133, 133, 133);
    border-radius: 5px 0px 0px 5px;
    font-weight: 600;

}
.iconsearch {
    margin-left: -3px;
    padding-top: 4px;
    background-color: rgb(0, 0, 0);
    color: white;
    width: 50px;
    height: 25px;
    text-align: center;
    border-radius: 0px 5px 5px 0px;
    transition: 0.5s;
}
.iconsearch:hover{
    background-color: yellow;
    color: black;
}
.boxlogin{
    display: flex;
    border: none;
    width: 20px;
    height: 25px;
    font-size: 20px;
    margin-right: 70px;
    padding-top: 3px ;
    text-transform: capitalize;
    color: rgb(99, 99, 99);
    transition: 0.5s;
}
.boxlogin:hover {
    color: black;
}
.login{
    margin-top: -5px;
    margin-right: 5px;
    padding-top: 2px;
    border: none;
    outline: none;
    font-size: 15px;
    font-weight: 600;
    background-color: rgb(255, 255, 255);
    color: rgb(99, 99, 99);
    text-transform: capitalize;
    transition: 0.5s;
}
.login:hover{
    color: black;
}
.last-header .inf_user{
    font-size: 14px;
    margin-top: 4px;
}
.iconlog {
    margin-right: 0px;
    padding-top: 0px;
    font-weight: bold;
}
.last-header{
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 80px;
    margin-bottom: 20px ;
    color: white;
    background-color: rgb(37, 37, 37);
    padding-top: 10px;
}
.info-user{
    display: flex;
}
.icon_user{
    padding-top: 10px;
    margin-right: 15px;
    margin-left: 19px;
    font-size: 30px;
}
.please{
    margin-top: 15px;
}

.nav {
    display: flex;
    justify-content: space-around;
    padding-top: 20px ;
    margin-right: 120px;
}
.nav ul{
    display: flex;
    list-style: none;

}

.nav .link{
    color: white;
    margin-right: 20px;
    text-decoration: none;
    text-transform: capitalize;
    font-weight: 600;
    font-family: sans-serif;
    font-size: 15px;
    transition: 0.5s;
}
.nav .link:hover{
    color: yellow;
}
.boxnav2 li {
    list-style: none;
    position: relative;
    left: 189px;
}
 .icon-cart{
    width: 50px;
    height: 45px;
    text-align: center;
    font-size: 27px;
    border-radius: 50%;
}
.icon-cart .icon-cart-link{
    text-decoration: none;
    color: rgb(77, 77, 77);
}
.icon-cart .icon-cart-link:hover{
    color: black;
}
.infop{
    margin-right: 15px;
    margin-top: 12px;
    padding-top: 9px;
    background-color: yellow;
    color: black;
    text-transform: capitalize;
    width: 150px;
    height: 35px;
    font-weight: 600;
    font-size: 12px;
    text-align: center;
    border-radius: 5px;
    
}









